.RightMenu{
    width: 250px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.VALUE{
    display: flex;
    justify-content: flex-end;
    width: 50%;
    margin-right: 70px;
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
}

.ChoseValue{
    top: 40px;
    box-shadow: 1px 4px 35px rgba(168, 172, 176, 0.19);
    background: white;
    position: absolute;
    z-index:  100;
}

.Currency{
    justify-content: center;
    width: 70px;
    cursor: pointer;
    display: flex;
}

.Currency p{
    display: flex;
    justify-content: center;
    text-align: center;
}

.RightMenu a{
    text-decoration: none;
    color: #1D1F22;
}

.Value{
    position: absolute;
    z-index:  100;
    top: 10px;
    margin-left: 10px;
    width: 15px;
    height: 15px;
    border-radius: 60px;
    background: #1D1F22;
}

.Value p{
    font-size: 12px;
    justify-content: center;
    display: flex;
    margin: 0;
    color: #E5E5E5;
}

.BAG{
    background: white;
    top: 49px;
    width: 320px;
    height: auto;
    position: absolute;
    border: #1D1F22 solid 1px;
}

.ewrgds{
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
}